import React, { Component } from "react";
import { Link } from "react-router-dom";
import foodmall from "../../Images/foodmall.jpg";
import bladeshouse from "../../Images/bladeshouse.jpg";
import bunglow from "../../Images/bunglow.jpg";
import courtbuilding from "../../Images/courtbuilding.jpg";
import enugenearcade from "../../Images/eugenearcade.jpg";
import gwaliorResort from "../../Images/gwaliorResort.jpg";
import MCTHouse from "../../Images/MCThouse.JPG";
import MCTInteriors from "../../Images/MCTInteriors.jpg";
import MLAhouse from "../../Images/MLAhouse.jpg";
import RenderingServices from "../../Images/RenderingServices.jpg";
import logo1 from "../../Images/logo1.png";
import "./Projects.css";

export default class Projects extends Component {
  Images = [
    {
      uri: foodmall,
      href: "",
    },
    {
      uri: bladeshouse,
    },
    { uri: bunglow },
    { uri: courtbuilding },
    { uri: enugenearcade },
    { uri: gwaliorResort },
    { uri: MCTHouse },
    { uri: MCTInteriors },
    { uri: MLAhouse },
    { uri: RenderingServices },
  ];
  render() {
    return (
      <div>
        <div className="row Tag-Line_outer no-gutters">
          <div className="col-md-4">
            <img className="logo" src={logo1}></img>
          </div>
          <div className="col-md-4">
            <h1 className=" text-center Tag-Line"></h1>
          </div>
        </div>
        <nav class="navbar navbar-expand-lg navbar-light bg-dark">
          <Link class="navbar-brand" to={"/"}></Link>
          <button
            class="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>

          <div class="collapse navbar-collapse " id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto">
              <li class="nav-item active">
                <Link class="nav-link" to={"/Projects"}>
                  SHOW ALL <span class="sr-only"></span>
                </Link>
              </li>
              <li class="nav-item">
                <Link class="nav-link" to={"/Projects/Bladeshouse"}>
                  BLADESHOUSE
                </Link>
              </li>
              <li class="nav-item">
                <Link class="nav-link" to={"/Projects/Bunglow"}>
                  BUNGLOW
                </Link>
              </li>{" "}
              <li class="nav-item">
                <Link class="nav-link" to={"/Projects/CourtBuilding"}>
                  COURT BUILDING
                </Link>
              </li>{" "}
              <li class="nav-item">
                <Link class="nav-link" to={"/Projects/Eugenearcade"}>
                  EUGENERCADE
                </Link>
              </li>{" "}
              <li class="nav-item">
                <Link to={"/Projects/Foodmall"} class="nav-link" href="#">
                  FOODMALL
                </Link>
              </li>{" "}
              <li class="nav-item">
                <Link class="nav-link" to={"/Projects/Gwaliorresort"}>
                  GWALIOR RESORT
                </Link>
              </li>{" "}
              <li class="nav-item">
                <Link class="nav-link" to={"/Projects/Mcthouse"}>
                  MCT HOUSE
                </Link>
              </li>
              <li class="nav-item">
                <Link class="nav-link " to={"/Projects/Mctinteriors"}>
                  MCT INTERIORS
                </Link>
              </li>
            </ul>
          </div>
        </nav>
        <div class="">
          <div class="row no-gutters">
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/Foodmall"}>
                <img src={foodmall} class="rounded  proImage " alt="..." />
              </Link>
            </div>
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/Eugenearcade"}>
                <img src={enugenearcade} class="rounded proImage  " alt="..." />
              </Link>
            </div>
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/Bladeshouse"}>
                <img src={bladeshouse} class="rounded  proImage " alt="..." />
              </Link>
            </div>
          </div>
          <div class="row  no-gutters">
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/Bunglow"}>
                <img src={bunglow} class="rounded  proImage " alt="..." />
              </Link>
            </div>
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/CourtBuilding"}>
                <img src={courtbuilding} class="rounded proImage  " alt="..." />
              </Link>
            </div>
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/Gwaliorresort"}>
                <img src={gwaliorResort} class="rounded  proImage " alt="..." />
              </Link>
            </div>
          </div>
          <div class="row no-gutters">
            <div class="col-md-4 main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={"/Projects/Mctinteriors"}>
                <img src={MCTInteriors} class="rounded  proImage " alt="..." />
              </Link>
            </div>
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={""}>
                <img src={MLAhouse} class="rounded proImage  " alt="..." />
              </Link>
            </div>
            <div class="col-md main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <Link to={""}>
                <img
                  src={RenderingServices}
                  class="rounded  proImage "
                  alt="..."
                />
              </Link>
            </div>
          </div>
          <div class="row no-gutters ">
            <div class="col-md-4 main_div">
              <div className="pro_image">
                <i class="plus_icon fa fa-plus fa-2x" aria-hidden="true"></i>
              </div>
              <a href="/Projects/Mcthouse">
                <img src={MCTHouse} class="rounded  proImage " alt="..." />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
