import React, { Component } from "react";
import Lightbox from "react-image-lightbox";

import pic1 from "../../Images/foodmall-baroda/Photo1.jpg";
import pic2 from "../../Images/foodmall-baroda/Photo3.jpg";
import "./Lightbox.css";
const images = [];

export default class LightboxModel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      photoIndex: 0,
      isOpen: false,
    };
    console.log(props);
  }
  componentDidMount() {
    if (this.props.path === "/Projects/Bladeshouse") {
      images.push(pic2, pic1);
    }
  }

  render() {
    const { photoIndex, isOpen } = this.state;

    return (
      <div>
        <button
          className="btn btn-primary"
          type="button"
          onClick={() => this.setState({ isOpen: true })}
        >
          Preview
        </button>

        {isOpen && (
          <Lightbox
            mainSrc={images[photoIndex]}
            nextSrc={images[(photoIndex + 1) % images.length]}
            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
            onCloseRequest={() => this.setState({ isOpen: false })}
            onMovePrevRequest={() =>
              this.setState({
                photoIndex: (photoIndex + images.length - 1) % images.length,
              })
            }
            onMoveNextRequest={() =>
              this.setState({
                photoIndex: (photoIndex + 1) % images.length,
              })
            }
          />
        )}
      </div>
    );
  }
}
