import React, { Component } from "react";
import { Link } from "react-router-dom";
import foodmall from "../../Images/foodmall.jpg";
import bladeshouse from "../../Images/bladeshouse.jpg";
import bunglow from "../../Images/bunglow.jpg";
import courtbuilding from "../../Images/courtbuilding.jpg";
import enugenearcade from "../../Images/eugenearcade.jpg";
import gwaliorResort from "../../Images/gwaliorResort.jpg";
import MCTHouse from "../../Images/MCThouse.JPG";
import MCTInteriors from "../../Images/MCTInteriors.jpg";
import MLAhouse from "../../Images/MLAhouse.jpg";
import RenderingServices from "../../Images/RenderingServices.jpg";

export default class Home extends Component {
  render() {
    return (
      <div>
        <div
          id="carouselExampleIndicators"
          class="carousel slide"
          data-ride="carousel"
        >
          {/* <span
            id="open"
            className="ham"
            style={{ fontSize: "30px", cursor: "pointer", marginLeft: "90%" }}
            onClick={this.openNav}
          >
            ☰
          </span> */}
          <ol class="carousel-indicators">
            <li
              data-target="#carouselExampleIndicators"
              data-slide-to="0"
              class="active"
            ></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
          </ol>
          <div class="carousel-inner">
            <div class="carousel-item active">
              <Link to={"/Projects/Foodmall"}>
                <img src={foodmall} class="" />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/Bladeshouse"}>
                <img src={bladeshouse} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/Bunglow"}>
                <img src={bunglow} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/CourtBuilding"}>
                <img src={courtbuilding} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/Eugenearcade"}>
                <img src={enugenearcade} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/Gwaliorresort"}>
                <img src={gwaliorResort} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/Mcthouse"}>
                <img src={MCTHouse} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <Link to={"/Projects/Mctinteriors"}>
                <img src={MCTInteriors} class="" alt="..." />
              </Link>
            </div>
            <div class="carousel-item">
              <img src={MLAhouse} class="" alt="..." />
            </div>
            <div class="carousel-item">
              <img src={RenderingServices} class="" alt="..." />
            </div>
          </div>
          <a
            class="carousel-control-prev"
            href="#carouselExampleIndicators"
            role="button"
            data-slide="prev"
          >
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="sr-only">Previous</span>
          </a>
          <a
            class="carousel-control-next"
            href="#carouselExampleIndicators"
            role="button"
            data-slide="next"
          >
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="sr-only">Next</span>
          </a>
        </div>
      </div>
    );
  }
}
